.error-container {
  position: relative;
  display: flex;
  align-items: center;
  background-color: #ffebee;
  color: #b71c1c;
  padding: 15px;
  border-radius: 10px;
  width: 70%;
  margin: 0 auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.error-icon {
  margin-right: 15px;
}

.error-message {
  margin: 0;
  font-size: 16px;
  font-weight: bold;
}

.show-modal-button {
  margin-left: auto;
  background-color: #f44336;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.modal {
  display: block;
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: 10% auto;
  padding: 20px;
  border: 1px solid #888;
  border-radius: 10px;
  width: 90%;
  max-width: 400px; /* Limit width for mobile devices */
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
}

.close-button {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.blur-background {
  filter: blur(3px);
}

/* Responsive styles for mobile devices */
@media (max-width: 768px) {
  .modal-content {
    width: 90%;
    max-width: 90%;
  }
}
