.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #d8e5e8;
  padding: 10px 20px;
}

.navbar__logo img {
  height: 60px; /* Adjust height as needed */
}

.navbar__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar__item {
  margin-right: 20px;
}

.navbar__item:last-child {
  margin-right: 0;
}

.navbar__item a {
  color: #0c2024;
  text-decoration: none;
}

.navbar__item a:hover {
  text-decoration: underline;
}

/* Media query for mobile */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
  }

  .navbar__list {
    margin-top: 20px;
  }

  .navbar__item {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
