.card {
  position: relative; /* Add position relative to allow absolute positioning within */
  width: 100%;
  max-width: 300px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  background-color: #d8e5e8;
  transition: transform 0.3s ease; /* Add transition for smooth hover effect */
  cursor: pointer; /* Add cursor pointer to indicate clickable */
}

.card:hover {
  transform: translateY(-5px); /* Add hover effect */
}

.card:active {
  transform: translateY(0); /* Add click effect */
}

.card__background {
  position: relative; /* Add position relative to allow absolute positioning within */
  height: 200px;
  background-size: cover;
  background-position: center;
}

.card__background::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 60px; /* Adjust the height of the gradient overlay */
  background: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.7)); /* Gradient colors */
}

.card__content {
  padding: 20px;
}

.card__header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.card__icon {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
}

.card__name {
  margin: 0;
}

.card__address {
  margin-bottom: 10px;
}

.card__flag-icon {
  width: 30px;
  height: 30px;
}

/* Media query for mobile */
@media (max-width: 768px) {
  .card {
    max-width: 100%;
  }
}
