.hotel-search-form {
  width: 100%;
  max-width: 500px; /* Adjust as needed */
  margin: auto;
  margin-top: 10%;
}

.input-wrapper {
  display: flex;
}

.search-input {
  flex-grow: 1;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  outline: none;
}

.search-button {
  padding: 10px 15px;
  background-color: #4285f4; /* Google's primary color */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 0px; /* Add space between input and button */
}

.search-button:hover {
  background-color: #357ae8; /* Darken the color on hover */
}

/* Media query for mobile */
@media (max-width: 768px) {
  .input-wrapper {
    flex-direction: column;
  }

  .search-input {
    margin-bottom: 10px; /* Add space between input and button */
  }
}
