.hotel-information {
  position: relative;
  background-color: #ffffff;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 70%;
  margin: 0 auto;
  margin-top: 5%;
  margin-bottom: 5%;
  padding: 20px;
  display: grid;
  grid-template-rows: auto auto auto; /* Three rows: button, image, text info */
  grid-template-columns: 1fr; /* One column */
  grid-gap: 20px; /* Gap between grid items */
}

.go-back-button {
  width: 100px;
  height: 40px;
  background-color: #333333;
  color: #ffffff;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  border-radius: 5px;
}

.hotel-information__photo {
  width: 100%;
  object-fit: cover;
  border-radius: 5px;
}

.hotel-information__details {
  display: flex;
  flex-direction: column; /* Stack items vertically */
  padding: 20px;
}

.hotel-information__name {
  margin: 0;
  font-size: 24px;
  color: #333333;
}

.hotel-information__rating {
  margin-bottom: 10px;
}

.star-icons {
  display: flex; /* Align stars horizontally */
}

.hotel-information__rating .fas {
  color: #ffd700;
}

.hotel-information__address {
  margin-bottom: 10px;
}

.hotel-information__text {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  color: #333333;
}

.columns {
  display: flex;
  justify-content: space-between; /* Separate columns */
}

.column {
  flex: 1; /* Equal width columns */
  margin-right: 20px; /* Add spacing between columns */
}

/* Font Awesome Stars */
.fa-star {
  margin-right: 3px;
}

/* Media query for mobile */
@media (max-width: 768px) {
  .hotel-information {
    width: 100%;
  }

  .hotel-information__photo {
    height: auto;
  }

  .hotel-information__name {
    font-size: 20px;
  }
}

.email-form {
  display: flex;
  flex-direction: column; /* Stack form items vertically */
  margin-top: 20px;
}

.email-input {
  width: 100%; /* Full width input */
  padding: 10px;
  margin-bottom: 10px; /* Add spacing between inputs */
}

.submit-button, .download-csv-button {
  width: 100%; /* Full width buttons */
  border-radius: 5px;
  background-color: #333333;
  color: #ffffff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}
