.container {
  width: 70%;
  margin: 0 auto;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.clear-search {
  padding: 8px 16px;
  background-color: #f44336;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.clear-search:hover {
  background-color: #d32f2f;
}

.Card-lists {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.Card {
  width: calc(33.33% - 20px);
  margin-bottom: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.Card:hover {
  transform: translateY(-5px);
}

@media screen and (max-width: 1024px) {
  .Card {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 768px) {
  .Card {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 480px) {
  .Card {
    width: calc(100% - 20px);
  }
}
